import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import {TOPNUMBERS, SIDENUMBERS} from './numbers.js';

const FILLED = 'ISFILLED';
const CROSSED = 'ISCROSSED';

function Square(props) {
    let className = 'square';
    let value = null;
    if (props.value === FILLED) {
        className += ' isFilled';
    } else if (props.value === CROSSED) {
        className += ' isCrossed';
    } else {
        value = props.value;
    }

    return (
        <button className={className} onClick={props.onClick}>
            {value}
        </button>
    );
}

class Board extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            squares: Array(15 * 15).fill(null),
            xIsNext: true,
        };
        this.values = [null, FILLED, CROSSED];
    }

    handleClick(i) {
        const squares = this.state.squares.slice();
        const valueIdx = this.values.indexOf(squares[i]);

        squares[i] = (valueIdx + 1) < this.values.length ? this.values[valueIdx + 1] : this.values[0];

        this.setState({
            squares: squares,
            xIsNext: !this.state.xIsNext,
        });
    }

    renderSquare(i) {
        return (
            <Square
                value={this.state.squares[i]}
                onClick={() => this.handleClick(i)}
                key={"s-" + i}
            />
        );
    }

    renderNumber(i, idx) {
        return (
            <Square
                value={i}
                key={"s-" + idx}
            >
            </Square>
        );
    }


    renderBox(num) {
        let res = [];
        for (var i = 0; i < 5; i++) {
            let inner = [];
            for (var j = 0; j < 5; j++) {
                inner.push(this.renderSquare(num));
                num += 1;
            }
            res.push(<li key={i}>{inner}</li>);
        }
        return (
        <div className="board-box">
            {res}
        </div>
        );
    }

    renderSide(num) {
        let res = [];
        for (var i = 0; i < 5; i++) {
            let inner = [];
            let nums = SIDENUMBERS[num + i];
            for (var j = 0; j < 6; j++) {
                inner.push(this.renderNumber(nums[j], j));
            }
            res.push(<li key={i} >{inner}</li>)
        }
        return (
            <div className="board-box side">
                {res}
            </div>
        );
    }

    renderTop(num) {
        let res = [];
        for (var i = 0; i < 5; i++) {
            let inner = [];
            let nums = TOPNUMBERS[num + i];
            for (var j = 0; j < 6; j++) {
                inner.push(<li key={j}>{this.renderNumber(nums[j], j)}</li>);
            }
            res.push(<div className="number-top" key={i} >{inner}</div>)
        }
        return (
            <div className="board-box">
                {res}
            </div>
        );
    }

    renderCorner() {
        let res = [];
        for (var i = 0; i < 6; i++) {
            let inner = [];
            for (var j = 0; j < 6; j++) {
                inner.push(<li key={j}><Square key={j}></Square></li>);
            }
            res.push(<div className="number-top" key={i} >{inner}</div>)
        }
        return (
            <div className="board-box">
                {res}
            </div>
        );
    }

    render() {
        return (
            <div>
                <div className="top">
                    {this.renderCorner()}
                    {this.renderTop(0)}
                    {this.renderTop(5)}
                    {this.renderTop(10)}
                </div>
                <div className="under">
                    <div className="number-side">
                        {this.renderSide(0)}
                        {this.renderSide(5)}
                        {this.renderSide(10)}
                    </div>
                    <div className="board">
                        <div>
                            {this.renderBox(0)}
                            {this.renderBox(25)}
                            {this.renderBox(50)}
                        </div>
                        <div>
                            {this.renderBox(75)}
                            {this.renderBox(100)}
                            {this.renderBox(125)}
                        </div>
                        <div>
                            {this.renderBox(150)}
                            {this.renderBox(175)}
                            {this.renderBox(200)}
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

class Game extends React.Component {
    render() {
        return (
            <div className="game">
                <div className="game-board">
                    <Board />
                </div>
                <div className="game-info">
                    <div>{/* status */}</div>
                    <ol>{/* TODO */}</ol>
                </div>
            </div>
        );
    }
}

// ========================================

ReactDOM.render(
    <Game />,
    document.getElementById('root')
);

function calculateWinner(squares) {
    const lines = [
        [0, 1, 2],
        [3, 4, 5],
        [6, 7, 8],
        [0, 3, 6],
        [1, 4, 7],
        [2, 5, 8],
        [0, 4, 8],
        [2, 4, 6],
    ];
    for (let i = 0; i < lines.length; i++) {
        const [a, b, c] = lines[i];
        if (squares[a] && squares[a] === squares[b] && squares[a] === squares[c]) {
            return squares[a];
        }
    }
    return null;
}
